import {
  FilterOutlined,
  PlusCircleOutlined,
  UploadOutlined,
  SearchOutlined,
  SyncOutlined,
  EditOutlined,
} from "@ant-design/icons";
import { Button, Col, PageHeader, Radio, Row, Select, Form, Tooltip, Upload } from "antd";
import SearchFilterDropdown from "components/shared-components/SearchFilter";
import TableComponent from "components/shared-components/TableComponent/Index";
import RestrictedContainer from "components/util-components/RestrictedContainer";
import designationFile from "assets/data/designation.xls";
import {
  ALL_STATUS,
  FILTER_FORM_FIELDS_OF_DESIGNATION,
  LIGHT_NAVY_BLUE_COLOR,
  NAVIGATION_ASSETS,
  NAVIGATION_ASSETS_RIGHTS,
  SCREEN_ITEMS_TYPES,
  SORT_DIRECTIONS,
  ACTION,
} from "constants/CommonConstants";
import {
  ACTIVE_STATUS,
  ADD_DESIGNATION,
  UPLOAD_DESIGNATION,
  DESIGNATION_FIELDS_NAME,
  DESIGNATION_TABLE_COLUMNS,
  INACTIVE_STATUS,
  MANAGE_DESIGNATIONS_TITLE,
} from "constants/ManageDesinations";
import useIntlMessage from "hooks/useIntlMessage";
import { useRef, useState } from "react";
import { useSelector } from "react-redux";
import manageDesignation from "services/DesignationService";
import Utils from "utils";
import AddDesignationForm from "./AddDesignationForm";

import { useDispatch } from "react-redux";
import UploadDesignation from "./uploadDesignationsFile";
import UploadData from "../../../../components/UploadData/index";
import { showLoader } from "redux/actions/Loader";
import useAuthRights from "hooks/useAuthRights";
const ManageAccounts = () => {
  const dispatch = useDispatch();
  const { hasAssetsRights } = useAuthRights();
  let searchInput = useRef(null);
  const [editDesignationData, setEditDesignationData] = useState(null);
  const [searchText, setSearchText] = useState("");
  const [searchedColumn, setsearchedColumn] = useState("");
  const allLocations = useSelector(state => state.myTeams.allLocations);
  const [value, setValue] = useState(ACTIVE_STATUS.value);
  const [countryValue, setCountryValue] = useState("");
  const [data, setData] = useState([]);
  const [filteredData, setFilteredData] = useState([]);
  const [filteredInfo, setFilteredInfo] = useState({});
  const [showDesignationModal, setShowDesignationModal] = useState(false);
  const getMessage = useIntlMessage();
  const { Option } = Select;
  const [sortedInfo, setSortedInfo] = useState({
    // order: "ascend",
    // columnKey: STATUS.value,
  });
  const [form] = Form.useForm();
  const [show, setShow] = useState(false);
  const ALL = "All";

  const getDesignationList = async (countryValue, value) => {
    try {
      dispatch(showLoader(true));
      const response = await manageDesignation.getList({ country: countryValue, status: value });
      value === ALL_STATUS.value
        ? setFilteredData(response)
        : setFilteredData(response?.filter(el => el.activeInd === value));
      setFilteredData(response);
      setData(response);
    } catch (e) {
      console.error("Unable to populate Designation list", e);
    } finally {
      dispatch(showLoader(false));
    }
  };

  const handleFetchDesignations = () => {
    if (countryValue) {
      setValue(ACTIVE_STATUS.value);
      getDesignationList(countryValue, ACTIVE_STATUS.value);
      setSearchText("");
      setsearchedColumn("");
      setFilteredInfo({});
      setSortedInfo({});
      handleReset();
    }
  };
  const onChangeStatus = e => {
    setValue(e.target.value);
    form
      .validateFields()
      .then(() => {
        if (countryValue && value !== undefined && value !== null) {
          getDesignationList(countryValue, e.target.value);
        }
      })
      .catch(e => {
        console.error(e);
      });
  };
  const onChangeCountry = e => {
    setCountryValue(e);
    getDesignationList(e, value);
  };

  const handleChange = (pagination, filters, sorter) => {
    setFilteredInfo(filters);
    setSortedInfo(sorter);
  };
  const getColumnSearchProps = field => {
    let filterObj = {
      filterIcon: filtered => (
        <SearchOutlined
          id={`manage-designation-search-btn-${field.key}`}
          color={filtered ? LIGHT_NAVY_BLUE_COLOR : undefined}
        />
      ),
    };
    if (field.type === "select") {
      if (!Utils.isEmpty(field.options)) {
        filterObj.filters = field.options.map(option => ({
          text: getMessage(option.label),
          value: option.value,
        }));
      }
      filterObj.filterIcon = filtered => (
        <FilterOutlined
          id={`manage-designation-search-btn-${field.key}`}
          color={filtered ? LIGHT_NAVY_BLUE_COLOR : undefined}
        />
      );
      filterObj.onFilter = (value, record) => {
        const _value = value === "active" ? true : false;
        return record[field.key] === _value;
      };
    } else {
      filterObj.filterDropdown = ({ setSelectedKeys, selectedKeys, confirm, clearFilters }) => (
        <SearchFilterDropdown
          setSelectedKeys={setSelectedKeys}
          selectedKeys={selectedKeys}
          confirm={confirm}
          clearFilters={clearFilters}
          handleSearch={handleSearch}
          dataIndex={field.key}
          field={field}
          searchInput={searchInput}
          handleReset={handleReset}
          label={getMessage(field.label)}
          inputId={`manage-designnation-input-${field.key}`}
          resetBtnId={`manage-designnation-reset-btn-${field.key}`}
          searchBtnId={`manage-designnation-search-btn-${field.key}`}
          dataSource={filteredData}
        />
      );
      filterObj.onFilter = (value, record) => {
        return record[field.key]?.toString().toLowerCase().includes(value.toLowerCase());
      };
      filterObj.onFilterDropdownVisibleChange = visible => {
        if (visible) {
          setTimeout(() => searchInput && searchInput.select && searchInput.select());
        }
      };
    }
    return filterObj;
  };

  const handleSearch = (selectedKeys, confirm, dataIndex) => {
    confirm();
    setSearchText(selectedKeys[0]);
    setsearchedColumn(dataIndex);
  };

  const handleReset = (clearFilters, confirm) => {
    clearFilters?.();
    setSearchText("");
    confirm?.();
  };

  const AddDesignation = () => {
    setShow(true);
  };
  let handleCancel = e => {
    setShow(false);
  };
  const designationColunm = Object.values(DESIGNATION_TABLE_COLUMNS).map(field => {
    return {
      title: (
        <Tooltip title={getMessage(field.label)} placement="top">
          <span>{getMessage(field.label)}</span>
        </Tooltip>
      ),
      dataIndex: field.key,
      key: field.key,
      ellipsis: true,
      width: field.width || null,
      filteredValue: filteredInfo[field.key] || null,
      sorter: (a, b) => Utils.antdTableSorter(a, b, field.key),
      sortDirections: SORT_DIRECTIONS,
      sortOrder: sortedInfo.columnKey === field.key ? sortedInfo.order : null,
      ...getColumnSearchProps(field),
      render: (_, record) => {
        if (field.key === "activeInd") {
          return record?.activeInd === true
            ? getMessage(DESIGNATION_FIELDS_NAME.ACTIVE.label)
            : getMessage(DESIGNATION_FIELDS_NAME.IN_ACTIVE.label);
        }
        return (
          <Tooltip title={record[field.key]} placement="top">
            <span>{record[field.key]}</span>
          </Tooltip>
        );
      },
    };
  });
  if (hasAssetsRights(NAVIGATION_ASSETS.ManageDomains, NAVIGATION_ASSETS_RIGHTS.UPDATE)) {
    designationColunm.push({
      title: ACTION.text,
      key: ACTION.value,
      width: 100,
      render: (_, record) => (
        <Tooltip title={getMessage("common.label.edit")}>
          <Button onClick={() => onEditDesignation(record)} size="small">
            <EditOutlined />
          </Button>
        </Tooltip>
      ),
    });
  }
  const onEditDesignation = record => {
    setShowDesignationModal(true);
    setEditDesignationData({ ...record });
  };
  return (
    <>
      <Row align="middle" gutter={[20, 20]} className="mb-2">
        <Col xs={24} md={12} lg={18} xl={18}>
          <PageHeader
            className="p-2 pl-2"
            title={MANAGE_DESIGNATIONS_TITLE.text}
            subTitle={
              <Button className="ml-3" onClick={handleFetchDesignations} disabled={!countryValue}>
                <SyncOutlined shape="circle" />
              </Button>
            }
          />
        </Col>
        <RestrictedContainer
          assetId={NAVIGATION_ASSETS.ManageDesignations}
          assetRight={NAVIGATION_ASSETS_RIGHTS.CREATE}
        >
          <Col className="mb-2 text-xl-right text-md-right " xs={24} md={24} lg={6} xl={6}>
            <Button type="primary" onClick={AddDesignation}>
              <PlusCircleOutlined className="mr-2" />
              {UPLOAD_DESIGNATION.text}
              {/* {ADD_DESIGNATION.text} */}
            </Button>
          </Col>
        </RestrictedContainer>
      </Row>
      <Row align="middle" gutter={[20, 20]} className="mb-2" justify="end">
        <Col xs={24} md={12} lg={8} xl={8} className="mb-2">
          <Form name="validateForm" form={form}>
            <Form.Item
              name="manage-project-select-account"
              label={getMessage("common.label.select-country")}
              rules={[{ required: true, message: getMessage("common.label.please-select-country") }]}
              className="mb-0"
            >
              <Select
                className="w-100"
                placeholder={getMessage(FILTER_FORM_FIELDS_OF_DESIGNATION.SELECT_COUNTRY.label)}
                name={FILTER_FORM_FIELDS_OF_DESIGNATION.CITY.name}
                onChange={onChangeCountry}
                showSearch
              >
                <Option value={ALL}>{ALL}</Option>
                {allLocations?.map(e => {
                  return (
                    <Option key={e?.country} value={e?.country}>
                      {e?.country}
                    </Option>
                  );
                })}
              </Select>
            </Form.Item>
          </Form>
        </Col>
        <Col xs={24} md={12} lg={6} xl={6} className="mb-2 text-xl-right text-md-right text-xs-right ">
          <Radio.Group onChange={onChangeStatus} value={value}>
            <Radio value={ACTIVE_STATUS.value}>{ACTIVE_STATUS.text}</Radio>
            <Radio value={INACTIVE_STATUS.value}>{INACTIVE_STATUS.text}</Radio>
            <Radio value={ALL_STATUS.statusValue}>{ALL_STATUS.text}</Radio>
          </Radio.Group>
        </Col>
      </Row>
      <TableComponent
        rowKey={record => record.designationId}
        columns={designationColunm}
        data={filteredData}
        scroll={{ x: true }}
        handleChange={handleChange}
        tableSize="small"
        showDropdown={false}
        showSorterTooltip={false}
      />
      {showDesignationModal && (
        <AddDesignationForm
          data={editDesignationData}
          setIsVisible={showDesignationModal}
          onClose={() => {
            setShowDesignationModal(false);
          }}
          onSuccess={() => {
            if (countryValue) {
              //   getDesignationList(true);
              handleFetchDesignations();
            }
          }}
        />
      )}

      {show && (
        <UploadData
          visible={show}
          onCancel={handleCancel}
          screen={SCREEN_ITEMS_TYPES.designation}
          sampleFile={designationFile}
        />
      )}
    </>
  );
};

export default ManageAccounts;
